import { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { Dialog, DialogContent, Button } from "@material-ui/core"

import { useUser } from "src/providers/user"
import apollo from "src/helpers/apollo"
import { USER_UPDATE } from "src/graphql/queriesUser"
import { handleError } from "src/helpers/errors"

const PrivacyPolicy = () => {
  const [open, setOpen] = useState(false)
  const { user, refetchUser } = useUser<true>()

  useEffect(() => {
    if (!user?.affichageDonneesPersonnelles) {
      const dismissed = localStorage.getItem("privacy_police_dismissed")
      if (!dismissed) {
        setOpen(true)
      }
    }
  }, [user?.affichageDonneesPersonnelles])

  const handleClose = () => {
    setOpen(false)
  }

  const handleDismiss = () => {
    setOpen(false)
    localStorage.setItem("privacy_police_dismissed", "true")
  }

  const handleAccept = async () => {
    setOpen(false)
    try {
      await apollo.mutate({
        mutation: USER_UPDATE,
        variables: { affichageDonneesPersonnelles: true, idUser: user?.id },
      })
      refetchUser()
    } catch (error) {
      handleError(error as Error)
    }
  }

  return (
    <Dialog open={open} fullWidth>
      <DialogContent>
        <p>
          {" "}
          Par dfaut, tes coordonnes (email et numro de tlphone) ne sont pas visibles par les autres
          cooprateursrices. Les rendre accessibles facilite la communication entre cooprateursrices et optimise
          lorganisation des PIAF au magasin, par exemple en cas dindisponibilit au dernier moment.{" "}
        </p>
        <p>
          Ce choix est modifiable  tout moment depuis <Link to="/profile"> mon profil</Link>
        </p>
      </DialogContent>

      <Button onClick={handleAccept} color="primary">
        Je comprends et jaccepte que mes coordonnes soient visibles par toustes les cooprateursrices
      </Button>
      <Button onClick={handleClose} color="primary">
        Je dcide plus tard
      </Button>
      <Button onClick={handleDismiss} color="primary">
        Je naccepte pas que mes coordonnes soient visibles
      </Button>
    </Dialog>
  )
}

export default PrivacyPolicy
